.editor.full-screen-editor, .nft-mode {
    width: 100%;
    // overflow: visible !important;

    .z-object.custom-fill {
        overflow: hidden;
    }
    .z-object.selected {
        .remove-from-fullscreen {
            position: absolute;
            top: -40px;
            right: 0px;
            width: 180px;
            padding: 5px;
            background-color: #9d9ff069;
            font-size: 9pt;
            text-align: center;
        }
    }

    .z-object.selected, .z-object.include-in-prsentation.selected {
        border: 1px solid white;
    }

    .editor {
        overflow: visible;
    }
    .expand-icon {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 20px;
        height: 20px;
        z-index: 1000;
        cursor:  pointer;
     }

    .z-object.include-in-presentation {
        box-shadow: unset !important;
    }

    .multi-full-screen {
        position: relative;
        left: unset !important;
        top: unset !important;
        margin: auto;
        display: flex;
        //overflow: visible;
        //overflow: visible !important;
       
        .z-object.main-nft {
            overflow: visible !important;
            align-items: flex-start;
            margin: 10px;
        }
    }
}
