.v3 {
    .zequencer-dock.showing {
        opacity: 1;
        height: 80px;
        width: 800px;
        left: 0px;
        right: 0px;
        margin-left: auto;
        margin-right: auto;
        animation-name: show-dock;
        animation-duration: 0.5s;
    }

    .zequencer-dock {
        height: 20px;
        opacity: 0;
        position: absolute;
        bottom: 10px;
        background-color: red;
        border-radius: 10px;
        width: 100%;
        display: flex;
    }
}

@keyframes show-dock {
    from {
        height: 20px;
        opacity: 0;
    }

    to {
        height: 80px;
        opacity: 1;
    }
}
