
.v3.nft-mode {
    .editor {
        background-color: unset !important;
        display: flex;
        flex-direction: column;

        .click-to-start {
            position: absolute;
            z-index: 10000000000;
            width: 100%;
            cursor: pointer;
            font-size: 32pt;
            font-family: stem;
            height: 100%;
            display: flex;
            background-color: #0000008a;
            
            span {
                margin: auto;
            }
        }
    }
}

.nft-viewer {
    display: flex;
    flex: 1;
    width: 100%;
    height: 100%;
    background-color: #090909;

    .expand-icon {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 20px;
        height: 20px;
        cursor: pointer;
        z-index: 10000000;
    }

    .z-object.main-nft {
        position: relative;
        left: unset !important;
        top: unset !important;
        margin: auto;
    }

    .z-object {
        .top-left-edge, .bottom-left-edge, .top-right-edge, .bottom-right-edge {
            display: none !important;
        }
        .context-menu, .inlets, .outlets {
            display: none !important;
        }
    }
}
