$mix-color1: #242424;
$mix-color2: #09243b;
$mix-color3: #09243b;
$gradient: linear-gradient(#16399d, #1648ff);
$gradient2: linear-gradient(#161924, #0b0b0d);
$gradient3: linear-gradient(#13182a, #060613);
$mix-component-size: 80px;

.mix-view {
    display: flex;
    flex-direction: column;
    height: 100%;

    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; 

    .lfo-component {
    }
}

.channel-mixer {
    margin-top: auto;
    width: 100%;
    width: 100%;
    z-index: 1000000000000000;

    .mix-bottom-bar {
        display: flex;
        background-color: #64636373;
        padding: 3px;
        width: 100%;
        .collapse-button {
            background-color: black;
            margin-left: 5px;
            width: 20px;
            height: 20px;
            padding-top: 4px;
            border-radius: 15px;
            cursor: pointer;

            .arrow-right {
                width: 0; 
                height: 0; 
                border-top: 5px solid transparent;
                border-bottom: 5px solid transparent;
                border-left: 9px solid #a2a2a2;
                margin-left: 6px;
            }
            
            .arrow-up {
                width: 0; 
                height: 0; 
                margin-left: 5px;
                border-left: 5px solid transparent;
                border-right: 5px solid transparent;
                border-bottom: 9px solid #a2a2a2;
            }
        }
    }
}

.channel-mixer-parts {
    display: flex;
}

.groups-container {
    display: flex;
    overflow: scroll;
    margin-right: 5px;
}

.mix-sends {
    display: flex;
    flex: 1;
    padding-left: 20px;
    border-style: solid;
    border-color: $mix-color2;
    background-color: black;
}

.channel-mixer.lower {
    bottom: 30px;
    width: 2000px;
}

.mixer-toolbar {
    padding-top: 5px !important;
}

.multi-parameter-slider-container > .ranged-slider-container{
    margin-left: 10px;
    height: 121px;
    width: 270px;
    border-color: #6e050c;
    border-style: solid;
    float: left;
    border-width: thin;
    padding-top: 35px;
    cursor: pointer;
    background-color: black;
}

.multi-parameter-slider-container > .parameters-container {
    background-color: black;
    margin-left: 5px;
    height: 121px;
    width: 161px;
    border-color: #6e050c;
    border-style: solid;
    border-width: thin;
    float: left;
    cursor: pointer;
}

.parameters-container > .parameters-title {
    font-size: 12pt;
    margin-left: 5px;
    float: left;
}

.parameters-container > .add-parameter-button {
    width: 50px;
    text-align: center;
    font-size: 20pt;
    line-height: 12px;
    padding: 5px;
    float: right;
    color: #42f4f1;
    cursor: pointer;
}

.min-label {
    float: left;
    height: 30px;
    line-height: 26px;
    width: 30px;
    font-size: 6pt;
    border-color: #6e050c;
    border-style: solid;
    border-width: thin;
    text-align: center;
    margin-right: 8px;
    margin-left: 2px;
}

.max-label {
    float: left;
    height: 30px;
    width: 30px;
    line-height: 26px;
    font-size: 6pt;
    border-color: #6e050c;
    border-style: solid;
    border-width: thin;
    text-align: center;
    margin-left: 5px;
}

.selected.max-label {
    border-color: #00ffcc;
}

.selected.min-label {
    border-color: #00ffcc;
}

.slider-container {
    float: left;
    text-align: center;
}

.parameter {
    font-size: 10pt;
}

.group-thumbnail {
    height: $mix-component-size;
    width: $mix-component-size;
    display: flex;
    flex-direction: row;
    border-color: $mix-color1;
    border-style: solid;
    border-width: thin;
}

.group-mute-button {
    background-image: $gradient;
    height: 52px;
    border-style: solid;
    border-color: $mix-color1;
    border-width: thin;
    cursor: pointer;
}

.group-top-buttons {
    margin-top: 5px;
    width: $mix-component-size;
    height: 62px;
    border: 1px solid $mix-color2;
}

.small-group-mute-button {
    height: 26px;
    background-image: $gradient;
    cursor: pointer;
    border-style: solid;
    border-color: $mix-color1;
    border-width: thin;
}

.muted.small-group-mute-button {
    background-color: black;
    background-image: none;
}

.muted.group-mute-button {
    background-color: black;
    background-image: none;
}

.selected.group.with-tracks {
    border-style: solid;
    border-color: #105aff;
    background-image: linear-gradient(#002246, #000000);
}

.group.with-tracks {
    border: 1px solid $mix-color2;
    background: $gradient3;
}

.hide.effects-pane {
    height: 30px;
}

.effects-pane {
    display: flex;
    width: 100%;
    border-top-color: $mix-color3;
    border-top-width: 1px;
    border-top-style: solid;
    bottom: 0px;
    clear: both;
    height: 230px;
    background: linear-gradient(#000000, #111113);
}

.group {
    bottom: 0px;
    border-radius: 2px;
}

.thumbnail-part {
    bottom: 50px;
    max-width: 100%;
    flex: 1 1;
}

.thumbnail-part-container {
    height: $mix-component-size;
    flex: 1 1;
    cursor: pointer;
    border-style: solid;
    border-color: $mix-color3;
    border-width: thin;
    position: relative;
}

.thumbnail-part-container.selected-track {
    border-style: solid;
    border-color: #47aaff;
}

.thumbnail-part-container.selected {
    border-style: solid;
    border-width: thin;
    border-color: $mix-color1;
}

.blank-thumbnail {
    background-color: red;
}

.thumbnail-part.unselected {
    opacity: 0.8;
}

.volume-slider-container  .slider {
    margin-left: 35%;
}

.volume-slider-container .slider-track {
    width: 15px !important;
}

.volume-slider-container  {
    height: $mix-component-size !important;
}

.volume-slider-container {
    .slider.slider-vertical {
        margin-top: 5px;
        height: 75px !important;
    }
}

.volume-slider-container .slider-handle {
    width: 40px !important;
    background-image: linear-gradient(#eeeeef, #b2edff) !important;
    margin-left: -7px;
}

.slider-container .slider-track {
    height: 15px !important;
}

.slider-container  .slider.slider-vertical {
}

.slider-container .slider-handle {
    height: 80px !important;
    background-image: linear-gradient(#004256, #014613) !important;
    margin-left: -6px;
}

.group .tags-container {
    clear: both;
    width:$mix-component-size;
    margin-bottom: 10px;
    font-size: 12px;
    overflow: scroll;
}

.outputs-container {
    font-size: 9pt;

    .Dropdown-control {
        border: none;
        background-color: #ffffff1c !important;
        color: white !important;

        .Dropdown-arrow {
            border-color: #fff transparent transparent;
        }

    }

    .Dropdown-menu {
        top: -1000%;
    }
}

.toggle-buttons-container {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
}

.track-toggle-button {
    cursor: pointer;
    border-style: solid;
    border-color: $mix-color1;
    border-width: thin;
    height: 13px;
    flex: 1;
}

.toggled.track-toggle-button {
    background-image: linear-gradient(#ff573c, #ff1010);
}

.track-mute-buttons-container {
    display: flex;
    flex-direction: row;
    height: 24px;
    flex-grow: 1;
}

.track-mute-button {
    cursor: pointer;
    border-style: solid;
    border-color: $mix-color1;
    border-width: thin;
    background-image: $gradient;
    flex: 1;
}

.muted.track-mute-button {
    background-color: black;
    background-image: none;
}

.mix-sends {
    .send-name {
        text-align: center;
        font-size: 12pt;
        background-color: #111326;
    }
}

.parameters-list {
    clear: both;
    font-size: 8pt;
    margin-left: 10px;
}

.selected.parameter-lock {
    border-color: #00c4ff;
    border-style: solid;
    border-width: thin;
}

.effects-pane-sidebar {
    float: left;
    display: flex;
    flex-direction: column;
    height: 100%;
    border-style: solid;
    border-width: thin;
    border-color: $mix-color3;
}

.effects-pane-sidebar > .sidebar-option {
    border-style: solid;
    border-width: thin;
    border-color: $mix-color3;
    padding: 4px;
    font-size: 8pt;
    cursor: pointer;
}

.selected.sidebar-option {
    background-color: $mix-color3;
}

.track-parameters {
    margin-left: 10px

}
.parameters-box {
    float: left;
    background-color:#061a29;
    height: 200px;
    margin-left: 10px;
    padding: 10px;
}

.parameter > .parameter-name {
    font-size: 10pt;
}

.parameter > .parameter-slider {
    margin-bottom: 4px;
}

.parameter.selected {
    background-color: black;
}

.mixes-container {
    float: left;
}

.mixes-title {
    font-size: 12pt;
    margin-left: 5px;
}

.mixes-box {
    background-color: black;
    margin-left: 10px;
    height: 121px;
    width: 161px;
    border-color: $mix-color3;
    border-style: solid;
    border-width: thin;
    float: left;
    display: flex;
    flex-direction: column;
}

.mixes-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.mix-container {
    font-size: 8pt;
    margin-left: 10px;
    padding: 3px;
    cursor: pointer;
}

.mix-container.selected {
    border-color: #c7ffce;
    border-style: solid;
    border-width: thin;
}

.mix-information {
    clear: both;
}

.delete-mix-button {
    background-color: $mix-color3;
    cursor: pointer;
    font-size: 10pt;
    text-align: center;
    flex-grow: 1;
}

.new-group-box {
    margin-top: 150px;
    text-align: center;
    color: gray;
    border-style: dashed;
    border-width: thin;
    border-color: $mix-color3;
    width: $mix-component-size;
    height: $mix-component-size;
}

.mix-parameters-option.selected {
    background-color: #3103a0 ;
}

.mix-parameters-option {
    cursor: pointer;
    height: 30px;
    width: $mix-component-size;
    background-color: #black;
    text-align: center;
    font-size: 12pt;
    line-height: 27px;
    color: #f1f1f1;
    border-style: solid;
    border-width: 1px;
    border-color: #6e050c;
}

.sub-sequence-fader {
    width: 80px;
    bottom: 0px;
    border-radius: 2px;
    background-color: #151a1a;
    margin-left: 5px;
    display: flex;
    flex-direction: column;
    margin-right: 5px;

    .mix-patterns {
        max-height: 250px;
        overflow-y: scroll;
    }

    .volume-slider-container {
        background-color: #131313;
    }
}

.sub-sequence-fader.selected {

    .sub-sequence-info {
    }


    .volume-slider-container {
        background-color: #111625;
    }
}

.sub-sequence-info.muted {
    background: none;
}

.sub-sequence-info {
    cursor: pointer;
    background-color: white;
    color: black;
    margin-top: 6px;
    border: 1px solid #00606e;
    text-align: center;
    height: 20px;
    border-style: solid;
    line-height: 17px;
}

.sub-sequences-mix-container {
    display: flex;
}

.sub-sequence-pattern {
    cursor: pointer;
    border-style: solid;
    border-width: 1px;
    width: 100%;
    text-align: center;
    font-size: 9pt;
    height: 20px;
    border: 1px solid #171539;
}

.sub-sequence-pattern.current {
    color: black;
    background-color: white;
}

.sub-sequence-fader-and-info {
    margin-top: 20px;
    bottom: 0px;
    flex: 1;
    display: flex;
    flex-direction: column;
}

.sub-sequence-fader-empty {
    flex: 1;
    background-color: black;
}

.groups-component {
    display: flex;
}

@keyframes flash-roll {
    from { filter: brightness(1); }
    to { filter: brightness(2); }
}

.steps-visualizer {
    clear: both;
    position: relative;


    .roll-visualizer {
        top: -3px;
        height: 30px;
        opacity: 0.8;
        position: absolute;
        background-color: red;
        animation: flash-roll 1s 0s infinite;
    }

    .step.triggering {
        box-shadow: 0px 0px 2px 2px #ff5151;
    }

    .step {
        width: 30px;
        height: 20px;
        position: relative;
        background-color: #00000042;

        .trigger-dot {
            width: 8px;
            height: 8px;
            background-color: #ff0000;
            position: absolute;
            bottom: 1px;
            right: 2px;
            border-radius: 8px;
        }
    }

    .step.toggled {
        background-color: #4b72ffe0 !important;
    }

    .step.toggled.previous-step {
        background-color: #1d274a;
        opacity: 0.8;
    }

    .step.rolling-step {
        filter: brightness(2);
    }

    .step.current-step {
        border-color: red !important;
        background-color: red !important;
    }

    .previous-step {
        background-color: #1d274a;
        opacity: 0.6;
    }
}

.track.flex.mix-lfo-view {
    border-style: none !important;
}

.group-part-name {
    position: absolute;
    font-size: 8pt;
    color: black;
    bottom: 0px;
    width: 100%;
    text-align: center;
}

.mix-tab {
    display: flex;
    margin: 5px;

    .mix-tab-option.current {
        border-bottom-width: 6px;
        border-bottom-color: white;
    }

    .mix-tab-option {
        border: 1px solid $mix-color1;
        width: 40px;
        padding-top: 5px;
        text-align: center;
        border-top-left-radius: 5px;
        background-color: #151a1a;
        margin-right: 5px;
        cursor: pointer;
    }

    .mix-tab-option.selected {
        background-color: white;
        color: black;
    }
}

img.thumbnail-part {
    height: 75px;
}

.tracks-visualizer {
    display: flex;
    margin-top: 10px;

    .track-visualizer.triggering {
        border-bottom-style: solid;
        border-bottom-color: red;
        border-bottom-width : 6px;
    }

    .track-visualizer {
        opacity: 1;
        cursor: pointer;
        height: 60px;
        width: 60px;
        margin-right: 10px;
        margin-bottom: 10px;
        display: flex;
        position: relative;

        span {
            position: absolute;
            left: 5px;
            font-size: 9pt;
            letter-spacing: 1px;
            color: #0000007a;
        }
        

        img {
            height: 52px;
            width: 56px;
        }
    }
}
