.zobject-modal {
    .modal-content {
        background-color: #4f4b4b7d !important;
        backdrop-filter: blur(4px) !important;
        .btn-secondary {
            background-color: #000000 !important;
            color: white !important;
            border-radius: 30px !important;
        }

        .btn-secondary:active.btn-secondary:hover {
            transform: scale(1) !important;
            animation-name: click-btn !important;
            animation-duration: 0.2s;
        }
        
        .btn-secondary:hover {
            transform: scale(1.1);
            animation-name: hover-btn !important;
            animation-duration: 0.2s;
            cursor: pointer;
        }
 

        .modal-footer {
            border-top-color: #4c4c4c96 !important
        }
    }

    .modal-links {
        margin-top: 10px;
        border: 1px solid #4c4c4c96 !important;
        padding: 10px;
    }

    .modal-link {
        display: flex;
        width: 100%; 
        a {
            margin-left: auto;
        }
    }
}
